import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import {Container, CssBaseline, Fab, Fade, Tab, Tabs, useMediaQuery, useScrollTrigger, useTheme} from "@mui/material";
import PropTypes from "prop-types";
import {lightBlue} from "@mui/material/colors";
import {useState} from "react";
import DrawerSB from "./drawer_sb/DrawerSB";
import {Navigate} from "react-router-dom";

function ScrollTop(props) {
    const { children, window } = props;
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.
    const trigger = useScrollTrigger({
        target: window ? window() : undefined,
        disableHysteresis: true,
        threshold: 100,
    });

    const handleClick = (event) => {
        const anchor = (event.target.ownerDocument || document).querySelector(
            '#back-to-top-anchor',
        );

        if (anchor) {
            anchor.scrollIntoView({
                block: 'center',
            });
        }
    };

    return (
        <Fade in={trigger}>
            <Box
                onClick={handleClick}
                role="presentation"
                sx={{ position: 'fixed', bottom: 16, right: 16 }}
            >
                {children}
            </Box>
        </Fade>
    );


}

export default function BackToTop(props) {
    const [value,setValue] = useState();
    const [newDirectory, setNewDirectory] = useState(false);
    const theme = useTheme();
    const isMatch =useMediaQuery(theme.breakpoints.down('md'));
    const PAGES = ["Home", "Articles", "Create Article", "Account"];

    if(newDirectory){
        switch (value){
            case 0:
                return <Navigate to={"/"}/>;
                break;
            case 1:
                return <Navigate to={"/user/articles"}/>;
                break;
            case 2:
                return <Navigate to={"/user/article/create"}/>;
                break;
            case 3:
                return <Navigate to={"/user/account"}/>;
                break;
            default:
                return <Navigate to={"/"}/>;
        }
    }

    const changeDirectory = (e)=>{
        setNewDirectory(true);
    }

    return (
        <React.Fragment>
            <CssBaseline />
            <AppBar sx={{ background: "#211970"}}>
                <Toolbar>
                    <Typography variant="h6" component="div" sx={{marginRight: "15px"}}>
                        Study Buddy
                    </Typography>

                    {isMatch ? (<>

                        <DrawerSB/>
                    </>):(<>
                        <Tabs textColor="inherit" value={value} onChange={(e, value) => setValue(value)} onChange={changeDirectory} idicatorColor="secondary">
                            {PAGES.map((page, index) => (<Tab key={index} label={page} />))}

                        </Tabs>

                        <Button sx={{ marginLeft: "auto"}} variant="contained"><a className="nav-link" href="/login">Login</a></Button>
                        <Button sx={{ marginLeft: '10px'}} variant="contained"><a className="nav-link" href="/register">Sign up</a></Button>
                    </>)
                    }


                </Toolbar>
            </AppBar>
            <Toolbar id="back-to-top-anchor" />
        </React.Fragment>
    );
}

ScrollTop.propTypes = {
    children: PropTypes.element.isRequired,
    /**
     * Injected by the documentation to work in an iframe.
     * You won't need it on your project.
     */
    window: PropTypes.func,
};